<template>
<div>
    <div class="item noflex" v-for="(item,i) in list" :key="i">
        <label class="label">
            <b>{{item.name}}</b>
            <i 
                :data-key='xhKey(item,i)'
                :data-id="item.id" 
                :data-industryName="item.industryName" 
                :data-industryId="item.industryId" 
                :data-storeId="item.storeId"
                :data-storeName="item.storeName"
                :class="xhshow(item,i)"
                :data-options="item.options"
                :data-optionsOrders="item.optionsOrders"
                @click="type==opentype.detail?rt():checkTrue($event,item,i)"
            ></i>
        </label>
        <span class="flex-box" v-show="checks[i]['c'+i]">
            <div
                class="goods-color" 
                :class="xhclass(jtem,j)"
                v-for="(jtem,j) in item.values" 
                :ref="'check'+i+j"
                :id="'check'+i+j"
                :data-key='xhKey1(jtem,j)' 
                :data-parentId="item.id" 
                :data-orders="jtem.orders"
                :data-specsCode="jtem.specificationCode"
                :data-specsId="jtem.specificationNameId"
                :key="j" 
                @click="type==opentype.detail?rt():checkFn($event,jtem,i)">
                {{jtem.specificationValue}}
            </div>
        </span>
    </div>
</div>
</template>
<script>
import Exotic from '../../config.js'
import _$ from 'expose-loader?underscore!underscore';
import com from '../../assets/common.js'
//  import { Indicator } from 'mint-ui';
    export default {
        props:['tables','arrayInfor','cationArr','cationarro','type','token'],
        created(){
            let that=this;
            that.MintUi.Indicator.open({
                text: '获取规格中...',
                spinnerType: 'fading-circle'
            })
            Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.list,{}, 'get',function(res) {
                console.log('------获取规格成功-----------');
                console.log(res);
                that.MintUi.Indicator.close();
                if(res.code==0){
                    for(let i=0;i<res.list.length;i++){
                        that.checks.push({['c'+i]:false});
                        that.tables.push({
                            values:[],
                            name:res.list[i].name,
                            index:i,
                            storeName:res.list[i].storeName,
                            orders:res.list[i].orders,
                            optionsOrders:res.list[i].optionsOrders,
                            options:res.list[i].options,
                            industryName:res.list[i].industryName,
                            industryId:res.list[i].industryId,
                            price:'',
                            stock:'',
                            id:res.list[i].id
                        });
                    }
                    that.list=res.list;
                }
            })
            
        },
        data(){
            return{
                checks:[],
                specificationGroup:[],
                list:[],
                opentype:{add:Exotic.operationType.add,update:Exotic.operationType.update,detail:Exotic.operationType.detail}
            }
        },
        methods:{
            rt(){
                return true;
            },
            //显示选中的规格组
            xhshow(item,i){
                let that=this;
                let classN=''
                _$.each(this.arrayInfor,function(it,ii){
                    if(it.id==item.id){
                        classN='active';
                        //显示规格
                        that.checks[i]['c'+i]=true;
                        return;
                    }
                })
                return classN;
            },
            //显示选中的规格
            xhclass(jtem,j){
                let that=this;
                let  classN=''
                 _$.each(this.cationArr.checks_arr,function(item,i){
                    if(item.specificationCode==jtem.specificationCode){
                        classN='active';
                        return;
                    }
                })
                return classN;
            },
            //规格组点击状态key为选中
            xhKey(item){
                let key=1;
                _$.each(this.arrayInfor,function(it,i){
                    if(it.id==item.id){
                        key=0;
                    }
                })
                return key;
            },
            //规格点击状态key为选中
            xhKey1(jtem,j){
                /*
                    逻辑：

                */
                let that=this;
                let key=1;
                 _$.each(this.cationArr.checks_arr,function(item,i){
                    if(item.specificationCode==jtem.specificationCode){
                        key=0;
                    }
                })
                return key;
            },
            //规格组点击
            checkTrue(ev,item,i){
                /*
                    逻辑：
                        规格组有key的属性用来区分规格组是否选中，
                        key为1时是默认状态未选中，
                        点击时就要改变当前的key!=1，下次再点击时，
                        就会走key=0的流程。
                        选中时就要在记录规格组[arrayInfor]的数组中添加该选中的规格组.
                        该规格组数组是在父组件中带过来的，this.arrayInfor。
                        key!=1时的流程是取消选中了，样式变为空，
                        该规格组对应的规格里所有的规格都要清空样式，然后每个规格的点击状态key都要变回1;
                */
                //获取当前点击的key
                let that=this;
                let key=ev.target.dataset.key;
                //key为1时为没选
                if(key==1){
                    //key=0,规格组选中
                    ev.target.dataset.key=0;
                    //对应规格显示
                    that.checks[i]['c'+i]=true;
                    //样式为active
                    ev.target.className="active";
                    //选中的规格组
                    item.index=i;
                    //选中的规格组增加
                    that.arrayInfor.push(item);
                    that.$emit('changeKey',false);
                }else{
                    //选中的规格组在规格组数组中的索引
                    let index=com.contains(this.arrayInfor,item.id,'id');
                    //删除该取消选中的规格组
                    this.arrayInfor.splice(index,1);
                    //key=0,规格组不选
                    ev.target.dataset.key=1;
                    //样式为空
                    ev.target.className="";
                    //隐藏右边的规格
                    this.checks[i]['c'+i]=false;
                    //清空表格数据
                    this.tables[i].values=[];
                    //获取当前规格组下的所有规格
                    let domarr=ev.target.parentNode.parentNode.parentNode.children[i].children;
                    console.log(domarr);
                    let tdom=domarr[1].children;
                    //获取span下的所有子集
                    // for(let k=0;k<domarr.length;k++){
                    //     if(domarr[k].tagName=="SPAN"){
                    //         tdom=domarr[k].children;

                    //         break;
                    //         console.log(tdom);
                    //     }
                    // }
                    console.log(tdom);
                    //清除所有规格的active
                    for(let l=0;l<tdom.length;l++){
                        //alert(JSON.stringify(tdom[l]))
                        if(tdom[l].dataset.parentid==item.id){
                            tdom[l].className="goods-color";
                            tdom[l].setAttribute('data-key',1);
                        }
                    }
                    that.cationArr.cation=_$.uniq(that.cationArr.cation,'specificationNameId');
                    //删除选中规格组cation
                    let cati=com.contains(that.cationArr.cation,item.id,'specificationNameId');
                    that.cationArr.cation.splice(cati,1);
                     //删除选中规格组
                    let catiarr=com.contains(that.cationarro,item.id,'specificationNameId');
                    that.cationarro.splice(catiarr,1);
                    //删除选择规格组ID
                    let idi=com.contains(that.cationArr.bigCationId,item.id);
                    that.cationArr.bigCationId.splice(idi,1);
                    //删除选择规格
                    for(let val in that.cationArr.checks_arr){
                        if(that.cationArr.checks_arr[val].specificationNameId==item.id){
                             that.cationArr.checks_arr.splice(val,1);
                        }
                    }
                    //let checki=com.contains(that.cationArr.checks_arr,item.id,'specificationNameId');
                    //that.cationArr.checks_arr.splice(checki,1);
                    that.$emit('uniq',that.cationarro);
                    that.$emit('changeKey',false);
                }
            },
            //规格点击事件
            checkFn(ev,jtem,i){
                let that=this;
                let key=ev.target.dataset.key;
                let parentId=ev.target.dataset.parentId;
                //点击规格时规格组要加入到规格组选中的数组中
                if(key==1){
                    ev.target.dataset.key=0;
                    ev.target.className="goods-color active";
                    //push 规格
                    that.tables[i].values.push({
                        id:jtem.specificationNameId,
                        code:jtem.specificationCode,
                        name:jtem.specificationValue,
                        index:i,
                        orders:jtem.orders
                    });
                    //当前规格有相同的code的数据合并
                    that.tables[i].values=_$.uniq(that.tables[i].values,'code');
                    //选中的规格组push cation_obj
                    _$.each(that.arrayInfor,function(val,i){
                        var cation_obj = {
                            specificationName:val.name,
                            specificationNameId:val.id,
                        }
                        that.cationArr.cation.push(cation_obj);
                        that.cationarro.push(cation_obj);
                    })
                    //规格发生点击，触发事件uniq 
                    //规格组变动时合并specificationNameId相同的数组,就是避免重复的规格组放在数组里，数组里只能放不同的规格组
                    that.$emit('uniq',that.cationarro);
                    //已选中的规格组避免重复
                    that.cationArr.cation=_$.uniq(that.cationArr.cation,'specificationNameId');
                    //选中的规格组ID的数组
                    that.cationArr.bigCationId.push(jtem.specificationNameId);
                    //已选中的规格组ID避免重复
                    that.cationArr.bigCationId=_$.uniq(that.cationArr.bigCationId);
                    //选中的规格数组
                    that.cationArr.checks_arr.push(jtem);
                    //避免修改状态时加载表格数据的报错
                    that.$emit('changeKey',false);
                }else{
                    ev.target.dataset.key=1;
                    ev.target.className="goods-color";
                    //删除选中规格
                    let index=com.contains(that.tables[i].values,jtem.specificationCode,'code');
                    that.tables[i].values.splice(index,1);
                    //如果该规格组的规格被取消了，看看是否为该规格组最后一个选中的取消
                    //是最后一个的取消的话就重新赋予选中规格组的数组数据
                    _$.each(that.arrayInfor,function(val,i){
                        var cation_obj = {
                            specificationName:val.name,
                            specificationNameId:val.id
                        }
                        that.cationArr.bigCationId.push(val.id);
                        that.cationArr.cation.push(cation_obj);
                        that.cationarro.push(cation_obj);
                    })
                    //规格发生点击，触发事件uniq 
                    //规格组变动时合并specificationNameId相同的数组,就是避免重复的规格组放在数组里，数组里只能放不同的规格组
                    that.$emit('uniq',that.cationarro);
                    //选中的规格组ID的数组
                    that.cationArr.bigCationId=_$.uniq(that.cationArr.bigCationId);
                    //已选中的规格组ID避免重复
                    that.cationArr.cation=_$.uniq(that.cationArr.cation,'specificationNameId');
                    //去除当前点击取消的选中的规格
                    let checki=com.contains(that.cationArr.checks_arr,jtem.specificationCode,'specificationCode');
                    that.cationArr.checks_arr.splice(checki,1);
                    //避免修改状态时加载表格数据的报错
                    that.$emit('changeKey',false);
                }
            }
        }
    }
</script>
<style scoped lang="scss">

</style>




